@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;

  padding: 0 2%;
}
.color{
  background-color: aqua;
}
button{
 padding:.5rem 1.5rem;
 background-color:#120d31;
 color: honeydew;
 border-radius: 2rem;
 border: none;
 cursor: pointer;
 color: #ffffff;
font-weight: 900;

}
.counter{
  border-radius: 10px;
 padding:0.5rem ;
 margin-bottom: 1rem;
  background-color: rgb(255, 255, 255);
color: black;
font-weight: 900;
}
.vote{
  margin-top: 0%;
  padding: 1rem 0 ;
  font-weight: 900;
}
i{
  position: absolute;
  height: 25px;
  width: 25px;
  background-color:darkred;
  border-radius: 50%;
  /* display: inline-block; */
  top:10%;
  left:50%;
  -webkit-transform: translate(-50%,50%);
          transform: translate(-50%,50%);
  color:#ffffff;
  font-weight: 900;
}


.container {
  color: #000;

  /* background-color: rgb(189, 41, 41); */
}

.status {
  display: inline-block;
  margin-top: 20px;
  border: 1px solid #3967c3;
  box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.56);
}
.header {
  text-align: center;
  margin-top: 20px;
  box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.56);
  border: 1px solid #3967c3;
}
.contest {
  text-align: center;
  margin-top: 20px;
  background-color: rgba(218, 181, 181, 0.452);
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  /* grid-template-rows: repeat(4, 1fr); */
  grid-column-gap: 1rem;
  /* grid-row-gap: 1rem; */
  /* overflow: hidden; */
  box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.56);
}


.con {position: relative;
padding: .5rem;
height: 25vh;
  background-color: rgba(128, 255, 0, 0.116);
  margin:  1rem 0;
  display: grid;
  justify-content: center;
  box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.16);
}
.loader {

margin: 1rem;
  border: 3px solid green;
  border: 6px solid Light grey;
  width: 100px;
  height: 100px;
  border-radius: 50%;
  border-top: 6px solid blue;
  border-right: 6px solid green;
  border-bottom: 6px solid red;
  border-left: 6px solid pink;

  -webkit-animation: spin 2s linear infinite;

          animation: spin 2s linear infinite;
}

@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
